import {
    Component,
    EventEmitter,
    Input,
    OnInit,
    Output
} from '@angular/core';
import { SelectOption } from '@mckinsey-converge/ui';
import { BehaviorSubject } from 'rxjs';
import {
    ActivatedRoute,
    ParamMap,
    Router
} from '@angular/router';
import { deepDistinctUntilChanged } from '@mckinsey-converge/rx-utils';
import { BaseDirective } from '@mckinsey-converge/base';

@Component({
    selector: 'app-results-header',
    template: `
        <div class="mck-row results-header">
            <div class="mck-column mck-space-between mck-flex-1">
                <h1 class="results-header-text">Results</h1>
                <span class="results-header-matching-criteria"
                      *ngIf="resultsCount >= 0">Matching your search criteria ({{resultsCount}})</span>
            </div>
            
            <mck-toggle [options]="toggleOptions"
                        [selected]="selectedToggle | async"
                        (selectedChange)="onSelectedChange($event)"></mck-toggle>
        </div>
    `,
    styleUrls: ['./results-header.component.scss']
})
export class ResultsHeaderComponent extends BaseDirective implements OnInit {

    public toggleOptions: SelectOption<any>[] = [
        {
            value: 0,
            display: 'List'
        },
        {
            value: 1,
            display: 'Map'
        }
    ];

    public selectedToggle = new BehaviorSubject<SelectOption<any>>(this.toggleOptions[0]);

    @Input()
    public resultsCount: number | undefined;

    @Output()
    public selectedToggleChange = new EventEmitter<SelectOption<any>>();

    constructor(private router: Router,
                private route: ActivatedRoute) {
        super();
    }

    public ngOnInit(): void {
        this.selectedToggleChange.next(this.selectedToggle.value);

        this.subscription('paramsToUI', this.route.queryParamMap
            .pipe(deepDistinctUntilChanged())
            .subscribe((next: ParamMap) => {
                const retrievedOption = this.toggleOptions.find(o => o.display.toLowerCase() === next.get('display'));
                this.onSelectedChange(retrievedOption ?? this.toggleOptions[0]);
            }));
    }

    public onSelectedChange(option: SelectOption<any>) {
        this.selectedToggle.next(option);
        this.selectedToggleChange.next(option);


        // toggle route.
        this.router.navigate([], {
            relativeTo: this.route,
            queryParams: {
                display: option.display.toLowerCase()
            },
            queryParamsHandling: 'merge'
        });
    }

}
