import {
    Component,
    Inject,
    ViewEncapsulation,
} from '@angular/core';
import {
    Space,
    RoomResultsViewModel,
} from '@mckinsey-converge/data-common';
import { MAP_OVERLAY_DATA } from '@acaprojects/ngx-interactive-map';

interface SpaceWithReturnData extends Space {
    // callback?: Function,
    room?: RoomResultsViewModel,
    parentComponent?: any,
}
/**
 * Replaces the built-in {@link MapPinComponent} based on design.
 */
@Component({
    selector: 'app-map-overlay',
    template: `
        <div class="mck-column mck-center map-overlay-container">
            <div class="map-label">
                {{data.simple_name || 'No Label'}}
                <button mat-stroked-button color="primary" *ngIf="data.parentComponent"
                    class="mck-stroked-button mck-short mck-column mck-center mck-primary"
                    (click)="roomDataCallback()"
                    (touchend)="roomDataCallback()"
                >
                    View room detail
                </button>
            </div>
            <div class="map-icon"></div>
        </div>
    `,
    styleUrls: ['./map-overlay.component.scss'],
    encapsulation: ViewEncapsulation.Emulated
})
export class MapOverlayComponent {
    constructor(
        @Inject(MAP_OVERLAY_DATA) public data: SpaceWithReturnData,
        ) {
    }

    public roomDataCallback() {
        if (this.data.parentComponent.roomSelected && this.data.room) {
            this.data.parentComponent.roomSelected(this.data.room)
        }
    }
}
